@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@100;700&display=swap');

.widget-container {
    position: relative;
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(to right, #eb5b58, #fecf6d);
}

@media screen and (min-width: 600px) {
    .widget-container {
        font-size: 24px ;
        width: 100%;
        min-width: auto;
    }
}

@media screen and (max-width: 380px) {
    .widget-container {
        font-size: 14px;
    }
}

.background-stars {
    width: 100%;
    height: 100%;
    position: absolute;
    background: url("./static/bg-stars.svg");
}

.logo {
    padding: 0.5em 0.5em 0 0;
    align-self: flex-end;
}

.logo > img {
    width: 4em;
}

.bg-picture {
    margin: 0;
    padding: 0;
    position: relative;
}

.sections {
    display: flex;
    justify-content: center;
    gap: 1.8em;
    margin-top: -0.5em;
    padding: 0 2em 0 2em;
}

@media screen and (max-width: 380px) {
    .sections {
        gap: 1.4em;
    }
}

.section {
    display: flex;
    flex-direction: column;
    width: 6em;
}

.reopened-theaters-numbers {
    position: absolute;
    top: 2.1em;
    left: 1.8em;
}

.admissions-numbers {
    position: absolute;
    top: 2.8em;
    left: 0.95em;
}

.european-movies-numbers {
    position: absolute;
    top: 2.5em;
    left: 1.8em;
}

.padding-zeros {
    color: #fff3de;
}

.number {
    color: #eb5b58;
}

.heart {
    margin-top: 0.7em;
    padding: 0;
    position: relative;
    width: 6em;
    height: 6em;
    background: url("./static/heart.svg") no-repeat;
    background-size: 100%;
}

.clap {
    margin-top: 0.35em;
    padding: 0;
    position: relative;
    width: 6em;
    height: 6em;
    background: url("./static/clap.svg") no-repeat;
    background-size: 100%;
}

.visitors {
    margin: 0;
    padding: 0;
    position: relative;
    width: 6em;
    height: 6em;
    background: url("./static/visitors.svg") no-repeat;
    background-size: 100%;
}

.subtitle {
    font-family: 'Spartan', sans-serif;
    font-weight: 700;
    font-size: 0.5em;
    text-align: center;
    color: #fff3de;
}

.subtitle-clap {
    margin-top: -1.3em;
}

.subtitle-heart {
    margin-top: -2em;
}

.subtitle-visitors {
    margin-top: -0.6em;
}

.footer {
    display: flex;
    flex-direction: column;
    padding: 0.75em 0.5em 0.5em 0.5em;
    gap: 0.1em;
}

.period {
    color: #fff3de;
    font-size: 0.5em;
}

.comscore {
    font-family: 'Spartan', sans-serif;
    font-size: 0.35em;
    font-weight: 100;
    color: #fff3de;
}